import { useBufferHeight, useRefFrame } from '@anonymous.club/ui';
import { useMemo, useRef } from 'react';
import {
  Color,
  DoubleSide,
  EllipseCurve,
  Mesh,
  MirroredRepeatWrapping,
  Vector2,
  MeshStandardMaterial
} from 'three';
import { PI } from '../constants';

export function WorldBackDrop() {
  const ref = useRef<Mesh>();
  const buffer = useBufferHeight();
  const inc = 10;
  const NUM_POINTS = 64;
  const points = useMemo(() => {
    const bigR = buffer.diag * 2 + inc;
    return new EllipseCurve(buffer.diag * 0.75, 0, bigR, bigR, PI._1_2, PI._3_2, true, 0).getPoints(NUM_POINTS);
  }, [buffer.diag, inc]);
  useRefFrame(([mesh], [{ camera, clock }]) => {
    mesh.rotation.copy(camera.rotation);
    const mat = mesh.material as MeshStandardMaterial;
    const t = clock.getElapsedTime();
    mat.bumpScale = Math.sin(t * 0.5) * 0.2 + 0.15;
    mat.displacementBias = Math.sin(t * 0.3) * 100;
  }, [ref]);
  return (
    <mesh
      castShadow
      receiveShadow
      layers={0}
      ref={ref}
      // scale={[1.01, 1.01, 1.01]}
      >
      <latheGeometry args={[points, NUM_POINTS, PI._1_2, PI._1]} />
      <meshStandardMaterial
        // flatShading
        // transparent
        // wireframe
        // depthTest={false}
        attach="material"
        bumpScale={0.1}
        displacementBias={0}
        displacementScale={-2500}
        emissive={new Color(0xcebbbb).setHSL(0.02,0.91,0.9)}
        fog={true}
        metalness={0.05}
        // metalness={0.05}
        roughness={0.1}
        // side={DoubleSide}
        >
        <noiseTexture
          args={[256, 256]}
          // attach="map"
          attach="displacementMap"
          repeat-x={4}
          repeat-y={16}
          rotation={0.125}
          wrapS={MirroredRepeatWrapping}
          wrapT={MirroredRepeatWrapping}
          />
        <noiseTexture
          args={[1024, 64]}
          // attach="map"
          attach="bumpMap"
          repeat-x={4}
          repeat-y={16}
          rotation={0.125}
          wrapS={MirroredRepeatWrapping}
          wrapT={MirroredRepeatWrapping}
          />
        <noiseTexture
          args={[1024, 4]}
          // attach="map"
          attach="map"
          repeat-x={60}
          repeat-y={2.5}
          rotation={0.125}
          wrapS={MirroredRepeatWrapping}
          wrapT={MirroredRepeatWrapping}
          />
      </meshStandardMaterial>
    </mesh>
  );
}
