import { useHelper } from '@react-three/drei';
import { useThree } from '@react-three/fiber';
import { useEffect, useLayoutEffect, useRef } from 'react';
import { CameraHelper, Vector3 } from 'three';
import { OrbitControls, FirstPersonControls } from 'three-stdlib';
import { useBufferHeight, useRefFrame } from '../utils';
import { useRefEffect } from '../utils/useRefEffect';
import { useThreeCamera, useThreeGl } from '../utils/useThreeGl';
import {
  INITIAL_CAMERA_POSITION,
  INITIAL_CAMERA_POSITION_LENGTH,
  INITIAL_CAMERA_POSITION_LENGTH_SQ,
  MAX_CAMERA_DISTANCE
} from './INITIAL_CAMERA_POSITION';
import { PI } from '../constants';
export function InternalCamera() {
  const controls = useRef<OrbitControls>();
  const camera = useThreeCamera();
  const { domElement } = useThreeGl();
  const buffer = useBufferHeight();
  useEffect(() => {
    camera.position.copy(INITIAL_CAMERA_POSITION);
    if (camera.type === 'OrthographicCamera') {
      camera.far = 100000;
    }
  }, [camera]);

  useRefEffect(([controlsRef]) => {
    // controlsRef.target.set(0, 0, 0);
    // controlsRef.maxDistance = buffer.diag * 2.4 - 5;
    // controlsRef.minDistance = 1;
    // controlsRef.enablePan = false;
    // controlsRef.maxAzimuthAngle = 0.125 * Math.PI;
    // controlsRef.minAzimuthAngle = -0.125 * Math.PI;
    // controlsRef.minPolarAngle = 0.375 * Math.PI;
    // controlsRef.maxPolarAngle = 0.625 * Math.PI;
    // controlsRef.update();
    console.log(controlsRef);
  }, [controls], [buffer]);

  const camRef = useRef(camera);
  // useHelper(camRef, CameraHelper);
  return (
    <orbitControls
      args={[camera, domElement]}
      enablePan={false}
      maxDistance={buffer.diag * 2.4 - 5}
      maxPolarAngle={PI._5_8}
      minDistance={0}
      minPolarAngle={PI._3_8}
      ref={controls}
      rotateSpeed={2}
      target={0}
      />
  );
}
