const circleSlice = (arr: any[], count: number) => (idx: number) => {
  let i = idx;
  const out = [];
  while (out.length < count) {
    i %= arr.length;
    out.push(arr[i]);
    i++;
  }
  return out;
};

function TestInput({ value, setValue, ...props }: { value: number, setValue: Dispatch<SetStateAction<number>> } & JSX.IntrinsicElements['input']): JSX.Element {
  const onChange = useCallback(({ target: { value: v } }) => {
    const parsed = parseInt(v);
    if (!isNaN(parsed)) setValue(parsed);
  }, [setValue]);
  return (
    <input
      {...props}
      type="number"
      value={value}
      onChange={onChange}
      />
  );
}

