import '../world-environment/jsx-extensions';
import { Suspense } from 'react';
import { GlobeItem } from './GlobeItem';
/* eslint-disable-next-line */
export interface GlobeProps {
  imgs: string[]
  cols?: number
}

export function Globe(props: GlobeProps) {
  const { imgs, cols } = props;
  const numCols = Math.min(imgs.length, cols ?? 4);
  const numRows = Math.ceil(imgs.length / numCols);
  return (
    <>
      {imgs.slice(0, numCols * numRows).map((src, i) => (
        <GlobeItem
          {...{ src, index: i, numCols, numRows }}
          key={`${src}?${numCols}x${numRows}x${~~(i / numCols)}`}
          />
      ))}
    </>
  );
}

export default Globe;
