import { useCallback, useEffect, useLayoutEffect, useMemo, useRef, useState } from 'react';
import {
  Vector3, EllipseCurve,
  Mesh, LatheBufferGeometry, Float32BufferAttribute, Group, Plane
} from 'three';
import { useSpringRef, useSpring, useChain, animated } from '@react-spring/three';
import { useBufferHeight, useRefFrame } from '../utils';
import { useThreeCamera } from '../utils/useThreeGl';
import { ImageMaterial } from './ImageMaterial';
import { useRefEffect } from '../utils/useRefEffect';
import { verticalSlice } from './verticalSlice';

type GlobeItemOptionals = {
  index?: number;
} & {
  position?: [row: number, col: number];
};

type GlobeItemProps = GlobeItemOptionals & {
  src: string;
  size?: number;
  numRows: number;
  numCols: number;
};


export function GlobeItem(props: GlobeItemProps) {
  const gutter = 0.02;
  const { src, size = 250, numCols, numRows } = props;
  const { index = 0 } = props;
  const { position: [pX, pY] = [~~(index / numCols) % numRows, index % numCols] } = props;
  const NUM_ITEM_POINTS = 16;
  const halfDim = size / 2;
  const mRef = useRef<Mesh>();
  const [dim, setDim] = useState(0);
  const seed = useMemo(() => 60 * (0.5 + 2 * Math.random()), []);
  const buffer = useBufferHeight();
  const [origin, setOrigin] = useState(() => new Vector3(0, buffer.diag * 2 * (pX - numCols / 2), -buffer.diag));
  const head = useMemo(() => new Vector3(0, 0, 0), []);
  const scaleSpringRef = useSpringRef();
  const morphSpringRef = useSpringRef();
  const rotationSpringRef = useSpringRef();
  const [{ pH }] = useSpring(() => ({ pH: 0, config: { tension: 210, friction: 40, bounce: 0.05 }, default: true, ref: scaleSpringRef }));
  const [{ pos, mH }, pApi] = useSpring(() => ({ pos: 1, mH: 0, config: { tension: 120, friction: 30, mass: 1 + Math.random() }, delay: seed,default: true, ref: morphSpringRef, onRest: {
    pos: () => {
      if (!mRef.current) return;
      mRef.current.geometry.boundingSphere && setOrigin(mRef.current.geometry.boundingSphere?.center);
    }
  } }));
  const xStart = 0.5 * Math.PI;
  const xDelta = Math.PI / (numCols / 2);
  const yStart = 0.5 * Math.PI;
  const yDelta = 2 * Math.PI / numRows;
  const { rD, rM, rX, rZ } = useSpring({
    rD: 0.25 * Math.PI - xDelta * pY,
    // rM: (pX - (numRows - 1) / 2)
    rM: 0.1 * buffer.height * (pX - (numRows - 1) / 2),
    rZ: buffer.height * (numRows - (pX - (numRows - 1) / 2)),
    rX: Math.PI * 0.75 * (pX - (numRows - 1) / 2),
    // rX: -(yStart - yDelta * (pX - (numRows - 1) / 2))
    ref: rotationSpringRef,
  });
  useChain([scaleSpringRef, morphSpringRef, rotationSpringRef], [0.5, 0.2, 0]);
  const afterImageLoads = useCallback((img: ImageData) => {
    pH.start(1);
    pos.start(0);
    mH.start(1);

    // setDim((img.height / img.width) / (buffer.height / buffer.width));
    setDim(img.height / img.width);
    // pH.start((img.width / img.height) ** 1);
  }, [mH, pH, pos]);
  const ref = useRef<Group>();
  const lref = useRef<LatheBufferGeometry>();
  // useEffect(() => {
  //   if (dim === 0) return;
  //   pH.start(1);
  //   pos.start(0);
  //   mH.start(1);
  // }, [dim, mH, pH, pos]);
  // useLayoutEffect(() => {
  //   // console.log(pX, pY, numCols, numRows, (pX - (numRows - 1) / 2));
  //   // if (ref.current) {
  //   //   ref.current.layers.set(0);
  //   //   // console.log(ref.current.getWorldPosition(new Vector3()));
  //   // };
  //   // mH.start(1);
  // }, [pos, pH, src, pX, pY, numCols, numRows, mH]);

  const sliceFn = useMemo(() => verticalSlice(1, gutter), []);
  const sliceFn2 = useMemo(() => verticalSlice(numRows, gutter), [numRows]);
  const points = useMemo(() => {
    const r = buffer.diag * 2;
    const [phiStart, phiEnd] = sliceFn(0);
    const curve = new EllipseCurve(-r * 0.25, 0, r - gutter, r - gutter, phiStart, phiEnd, false, 0);
    // const curve = new EllipseCurve(bigR * 1.75, 0, 20 * bigR - gutter, 20 * bigR - gutter, phiStart, phiEnd, false, 0);
    const curvePoints = curve.getSpacedPoints(NUM_ITEM_POINTS);
    return curvePoints;
  }, [buffer.diag, sliceFn]);
  useRefFrame(([obj]) => {
    // if (!pos.isAnimating) return;
    const { position } = obj;
    position.lerpVectors(head, origin, pos.get());
    // position.lerpVectors(posCenter, cam.position.clone().negate(), pos.get());
    // if (obj.morphTargetInfluences) {
    //   obj.morphTargetInfluences[0] = mH.get();
    // }
    // else
    //   obj.updateMorphTargets();
    // obj?.lookAt(posCenter);
  }, [mRef]);
  useRefEffect(([geometry]) => {
    const r = buffer.diag;
    // const curve = new EllipseCurve(0, 0, 2 * bigR - gutter, 2 * bigR - gutter, 0.75 * Math.PI, 1.25 * Math.PI, false, 0);
    const [phiStart, phiEnd] = sliceFn2(pX);
    const curve = new EllipseCurve(-r * 0.5, 0, 2 * r - gutter, 2 * r - gutter, phiStart, phiEnd, false, 0);
    const curvePoints = curve.getSpacedPoints(NUM_ITEM_POINTS);
    const l2 = new LatheBufferGeometry(curvePoints, NUM_ITEM_POINTS * 2, xStart, xDelta - gutter);
    const l2P = l2.getAttribute('position').clone();
    if (!geometry.morphAttributes.position)
      geometry.morphAttributes.position = [];
    geometry.morphAttributes.position[0] = l2P;
    // geometry.morphAttributes.position[0] = new Float32BufferAttribute(planar, 3);
    // if (!geometry.morphAttributes.normal)
    //   geometry.morphAttributes.normal = [];
    // const l2N = l2.getAttribute('normal').clone();
    // geometry.morphAttributes.normal[0] = l2N;
    // const l2U = l2.getAttribute('uv').clone();
    // if (!geometry.morphAttributes.uv)
    //   geometry.morphAttributes.uv = [];
    // geometry.morphAttributes.uv[0] = l2U;
    l2.dispose();
  }, [lref], [lref.current, pX]);
  // useRefEffect(([geometry]) => {
  //   const position = geometry.getAttribute('position');
  //   if (!geometry.morphAttributes.position) geometry.morphAttributes.position = [];
  //   geometry.computeBoundingBox();
  //   geometry.boundingSphere ?? geometry.computeBoundingSphere();
  //   const box = geometry.boundingBox;
  //   const sphere = geometry.boundingSphere;
  //   const planar: number[] = [];
  //   const plane = new Plane(box?.getCenter(new Vector3()).normalize().negate(), sphere?.radius);
  //   const up = new Vector3(0, 1, 0);
  //   const vertex = new Vector3();
  //   const v2 = new Vector3();
  //   for (let i = 0; i < position.count; i++) {
  //     const x = position.getX(i);
  //     const y = position.getY(i);
  //     const z = position.getZ(i);
  //     vertex.set(x, y, z);
  //     // plane.projectPoint(vertex, v2);
  //     v2.lerp(vertex, 0.5);
  //     v2.projectOnPlane(up).toArray(planar, planar.length);
  //   }
  //   geometry.morphAttributes.position[0] = new Float32BufferAttribute(planar, 3);
  //   console.log(geometry, plane);
  // }, [lref], [lref.current]);
  // useHelper(mRef, VertexNormalsHelper, 100);
  const mti = useMemo(() => [], []);
  return (
    <animated.group
      castShadow
      receiveShadow
      layers={1}
      ref={ref}
      rotation-x={0}
      // rotation-z={Math.PI}
      rotation-y={rD}
      rotation-z={0}
      // scale={pH}
      scale-x={1}
      scale-y={1}
      scale-z={pH}
      >
      <animated.mesh
        castShadow
        receiveShadow
        // layers={0}
        // quaternion={new Quaternion()}
        // quaternion-w={rM}
        // lookAt={posCenter}
        // quaternion-y={0}
        // quaternion-z={0}
        morphTargetInfluences={mti}
        morphTargetInfluences-0={mH}
        position-z={rZ}
        ref={mRef}
        rotation-order="ZXY"
        rotation-x={0}
        rotation-y={0}
        rotation-z={Math.PI}
        scale={1}
        >

        <latheBufferGeometry
          args={[points, NUM_ITEM_POINTS * 2, xStart, gutter]}
          // args={[points, NUM_ITEM_POINTS * 2, xStart, xDelta - gutter]}
          ref={lref}
          >
          {/* <primitive
              attachObject={['morhpAttributes', 'position']}
              object={[]}
              /> */}
          {/* {morph && (<primitive
              attachObject={['morphAttributes', 'position', '0']}
              object={morph}
              />)} */}
        </latheBufferGeometry>
        <ImageMaterial
          delay={pX * numCols}
          src={src}
          onLoad={afterImageLoads}
          />
      </animated.mesh>
    </animated.group>
  );
}
