import { useSpring, animated } from '@react-spring/three';
import { Group } from 'three';
import { INITIAL_CAMERA_POSITION_LENGTH, INITIAL_CAMERA_POSITION_LENGTH_SQ } from '../world-environment/INITIAL_CAMERA_POSITION';
import { useRefFrame } from '../utils';
import { PI } from '../constants';

