const _overflow = PI._2 * 1000;
export const asHeroFigurine = (Component: ModelComponent, useStore: UseStore<WorldState>) => {
  return (props: ModelProps) => {
    const onHeroHover = useStore(state => state.onHeroHover);
    const onLoad = useStore(state => state.setHeroCenter);
    const groupRef = useRef<Group>();
    const modelRef=useRef<Group>();
    const { rY } = useSpring({ rY: 0, default: true });
    useRefFrame(([m], [{ camera }]) => {
      rY.start((rY.goal + 0.01) % _overflow);
      m.scale.setScalar(camera.position.length() / INITIAL_CAMERA_POSITION_LENGTH);
    }, [modelRef]);
    return (
      <group ref={modelRef}>
        <Suspense fallback={null}>
          <animated.group
            ref={groupRef}
            rotation-y={rY}
            onPointerOut={useCallback(() => onHeroHover({ down: false }), [onHeroHover])}
            onPointerOver={useCallback(() => onHeroHover({ down: true }), [onHeroHover])}
            >
            <Component
              {...props}
              onLoad={onLoad}
              />
          </animated.group>
        </Suspense>
      </group>
    );
  };
};

export default HeroFigurine;
