import { Bloom, ChromaticAberration, ColorAverage, DepthOfField, EffectComposer, Noise, Vignette } from '@react-three/postprocessing';
import { BlendFunction } from 'postprocessing/src/effects/blending/BlendFunction';
// import { EdgeDetectionMode } from 'postprocessing/src';
import { SMAA } from '@react-three/postprocessing';
import { useFrame } from '@react-three/fiber';
import { useRefFrame } from '../utils/useRefFrame';
import { useLayoutEffect, useRef } from 'react';
import { PI } from '../constants';

export function EnvironmentEffects() {
  const dof = useRef<Record<string, unknown>>();
  useRefFrame(([effect], [{ clock }]) => {
    const t = clock.getElapsedTime();
    // debugger;
    effect.bokehScale = (1 + Math.atan((t * -0.2) * (t - 2) + (0.5 * Math.sin(t * 2))) / PI._1_2) * 10 + 0.0;
  }, [dof]);
  // useLayoutEffect(() => {
  //   const effect = dof.current;
  //   debugger;
  // }, [dof.current]);
  return (
    <EffectComposer
      autoClear
      // depthBuffer
      multisampling={4}
      >
      {/* <ChromaticAberration
        blendFunction={BlendFunction.AVERAGE}
        /> */}
      {/* <DepthOfField
        blendFunction={BlendFunction.NORMAL}
        bokehScale={0.95}
        focalLength={0.85}
        />
      <Bloom
        blendFunction={BlendFunction.REFLECT}
        // height={32}
        luminanceSmoothing={0.1}
        luminanceThreshold={0.85}
        opacity={0.5}
        /> */}
      <Vignette
        darkness={0.925}
          // darkness={0.925}
        eskil={false}
        offset={0.00975}
        />
      <Noise
        // premultiply
        blendFunction={BlendFunction.AVERAGE}
          // opacity={0.95}
        opacity={0.03}
        />
      <DepthOfField
        blendFunction={BlendFunction.NORMAL}
        bokehScale={10}
        focalLength={0.9}
        focusDistance={0.1}
        // focalLength={0.01}
        ref={dof}
        />
    </EffectComposer>
  );
}
